        p Con la implementacin de programas se propone dinamizar procesos de desarrollo regional a partir de la gestin misional de las Corporaciones y las entidades territoriales de la regin, trascendiendo la intervencin puntual y aislada. Se trata de instaurar o fortalecer procesos orientados a promover el desarrollo sostenible regional, a partir de la solucin de los problemas ms apremiantes de la poblacin. Por tanto, los instrumentos con que se estructura la presente Estrategia buscan contribuir a propiciar una nueva lgica de gestin pblica sustentada y fortalecida en el empoderamiento de la sociedad civil para transformar su realidad al posibilitarle asumir responsabilidades y compromisos en el desarrollo territorial.
