      h2 2.1 Normativa
    
    p De acuerdo con la Corporacin Autnoma Regional del Valle del Cauca - CVC, en su Plan de gestin ambiental regional 2015 - 2036, la planificacin ambiental en Colombia est definida constitucionalmente en el artculo 80 de la Constitucin Poltica de Colombia, el cual establece que el Estado planificar el manejo y aprovechamiento de los recursos naturales, para garantizar su desarrollo sostenible, su conservacin, restauracin o sustitucin. En desarrollo de las disposiciones Constitucionales, la planificacin del desarrollo sostenible en general se ha regulado por tres leyes fundamentales:

