        p En desarrollo de las disposiciones Constitucionales, la planificacin del desarrollo sostenible en general se ha regulado por tres leyes fundamentales, que establece los instrumentos, criterios y elementos para la planificacin ambiental en el pas.
        p.mb-0 De acuerdo con el Establecimiento Pblico Ambiental - EPA de la ciudad de Cartagena, el marco normativo de la educacin ambiental est basado en:

