        p.mb-0 De acuerdo con el entonces Ministerio del Medio Ambiente (MMA) y el Ministerio de Educacin Nacional (MEN), en la Poltica Nacional de Educacin Ambiental (SINA), se establece que desde 1991, Colombia ha venido desarrollando una propuesta nacional de Educacin Ambiental, cuyos esfuerzos fundamentales han estado orientados a la inclusin de la temtica, tanto en el sector ambiental como en el sector educativo especficamente. En el sector educativo, la educacin ambiental se ha venido incluyendo como una de las estrategias ms importantes de las polticas, en el marco de la reforma educativa nacional y desde los conceptos de autonoma y descentralizacin. En el contexto anterior, se han logrado avances significativos en lo que tiene que ver con el proceso de institucionalizacin, tanto a nivel nacional como a nivel regional o local.

    p La Poltica Nacional de Educacin Ambiental nace en el mandato constitucional de 1995 dentro del Plan Nacional de Desarrollo (PND) Salto social; all se estableci que debe existir una poltica que ubique el desarrollo sostenible como una de las metas del desarrollo del pas, y la educacin ambiental como una de las estrategias primordiales para disminuir el deterioro del ambiente y contribuir al alcance de las metas del mencionado desarrollo. 
    p.mb-5 En 1996, resultado del proceso de institucionalizacin de la educacin ambiental impulsado por el programa, el Plan Decenal de Educacin (1996 - 2005), en su Proyeccin Nmero 11, incorpora la educacin ambiental como una perspectiva necesaria para contribuir en el mejoramiento de la calidad de vida del pas.

