    .row.justify-content-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p De acuerdo con el Decreto 1076 (2015), el Plan de Gestin Ambiental Regional es el instrumento de planificacin estratgico de largo plazo de las Corporaciones Autnomas Regionales para el rea de su jurisdiccin, que permite orientar su gestin e integrar las acciones de todos los actores regionales, con el fin de lograr que el proceso de desarrollo avance hacia la sostenibilidad de las regiones.
        .h5.texto-primario El Plan de Gestin Ambiental Regional tendr una vigencia de mnimo 10 aos.
        p Las Corporaciones Autnomas Regionales tienen la responsabilidad de la formulacin del PGAR, en coordinacin con las entidades territoriales de su jurisdiccin y los representantes de los diferentes sectores sociales y econmicos de la regin. El PGAR deber ser aprobado por el Consejo Directivo de la respectiva corporacin.
        p La Poltica Nacional de Educacin Ambiental se compone de:
