        .h4.text-white.mb-0 Plan de Gestin Integral de Residuos Slidos - PGIRS
    
    .row.justify-content-center.mb-4
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p El Decreto 2981 (2013) define el Plan de Gestin Integral de Residuos Slidos como el instrumento de planeacin municipal o regional que contiene un conjunto ordenado de objetivos, metas, programas, proyectos, actividades y recursos, definidos por uno o ms entes territoriales para el manejo de los residuos slidos, basado en la poltica de gestin integral de los mismos, el cual se ejecutar durante un perodo determinado, basndose en un diagnstico inicial, en su proyeccin hacia el futuro y en un plan financiero viable que permita garantizar el mejoramiento continuo del manejo de residuos y la prestacin del servicio de aseo a nivel municipal o regional, evaluado a travs de la medicin de resultados.
        p El PGIRS contiene un conjunto ordenado de objetivos, metas, programas, proyectos, actividades y recursos, definidos por uno o ms entes territoriales para el manejo de los residuos slidos, basado en la poltica de gestin integral de los mismos, el cual se ejecutar durante un perodo determinado.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img28.png' alt="Imagen decorativa")
    
    p De acuerdo con la resolucin 0754 (2014), el PGIRS debe estar compuesto por:

    AcordionA.mb-5(tipo="a" clase-tarjeta="BGR14")
      .row.justify-content-center(titulo="Organizacin para la formulacin de los PGIRS")
        .col-lg-11
          p.mb-0 Grupo interdisciplinario con experiencia en los aspectos tcnico-operativos, sociales, ambientales, legales, financieros y administrativos en la gestin integral de residuos slidos y del servicio pblico de aseo.
      .row.justify-content-center(titulo="Lnea base")
        .col-lg-11
          p.mb-0 Sirve para establecer el estado actual de la gestin integral de residuos slidos en el respectivo municipio, distrito o regin.
      .row.justify-content-center(titulo="Objetivos y metas")
        .col-lg-11
          p.mb-0 Como mnimo se espera que los objetivos propuestos permitan cumplir los principios bsicos para la prestacin del servicio pblico de aseo y la gestin integral de los residuos slidos.
      .row.justify-content-center(titulo="Programas y proyectos para la implementacin del PGIRS")
        .col-lg-11
          p.mb-0 Alternativas de solucin viable y sostenible, enfocadas en cada uno de los componentes del servicio, teniendo en cuenta los aspectos tcnicos, operativos, ambientales, institucionales, econmicos y financieros asociados.
      .row.justify-content-center(titulo="Cronograma")
        .col-lg-11
          p.mb-0 Indica las actividades generales y especficas de cada proyecto, el tiempo en el que se llevar a cabo y el responsable de cada actividad general y especfica.
      .row.justify-content-center(titulo="Plan financiero")
        .col-lg-11
          p.mb-0 Es la gua para la asignacin de recursos del municipio, distrito o regin y garantizar la viabilidad de proyectos que se proponen.
      .row.justify-content-center(titulo="Implementacin, evaluacin y seguimiento")
        .col-lg-11
          p.mb-0 Identificacin de avances y dificultades en la implementacin del PGIRS, junto con la definicin de todas las medidas correctivas a las que haya lugar.
      .row.justify-content-center(titulo="Revisin y actualizacin")
        .col-lg-11
          p.mb-0 Revisin realizada al inicio de cada periodo constitucional con el fin de realizar las mejoras tcnicas correspondientes.

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="flip-up")
        .BGR13.p-3
          .row.justify-content-around.align-items-center
            .col-4.col-sm-3.col-lg-2.px-xl-5.px-4
              img(src="@/assets/curso/temas/img01.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1 Plan de gestin integral de residuos slidos PGIRS
                  p.mb-0 Para conocer e identificar los componentes de un Plan de Gestin Integral de Residuos Slidos, se sugiere consultar el PGIRS de la ciudad de Bucaramanga.
                .col-sm-auto
                  a.boton.color-primario(:href="obtenerLink('/downloads/PGIRS_Bucaramanga.pdf')" target="_blank")
                    span(style="color: #000; font-weight: 400") Descargar
                    i.fas.fa-file-download(style="color: #000")
    
    .row.justify-content-start.mb-4
      .col-auto.BGRS01.px-5.py-3(data-aos="fade-right")
        .h4.text-white.mb-0 Plan Anual de Trabajo - PAT
    
    p El Plan Anual de Trabajo es un documento de seguimiento a las actividades ambientales que se van a llevar a cabo durante el ao en el que se planea. 
    p A travs de este plan se puede evidenciar el cumplimiento o no de las actividades, los responsables y las evidencias que se generan al cumplir la accin; para disear un plan de trabajo anual se recomienda:
    
    .row.justify-content-end.mb-5
      .col-lg-7(data-aos="fade-right")
        LineaTiempoD.color-acento-contenido
          div(numero="1" titulo="Establecer el programa al cual pertenece la actividad")
            p.mb-0 Esto permite conocer el componente ambiental en el que se trabaja y el presupuesto del cual se obtendrn los recursos, por ejemplo, programa uso eficiente de ahorro de agua.
          div(numero="2" titulo="Alcance")
            p.mb-0 Es la actividad que se va a realizar, se fija para evitar sobrecostos o demoras en la ejecucin por la realizacin de actividades no contempladas. Un ejemplo del alcance es cuantificar peridicamente el consumo del recurso agua, con el fin de tomar medidas para asegurar la utilizacin racional.
          div(numero="3" titulo="Objetivo de control")
            p.mb-0 Es la razn por la cual se realiza la actividad, es decir, qu se pretende con el alcance, ejemplo, monitorear y controlar el consumo del recurso hdrico.
          div(numero="4" titulo="Metas")
            p.mb-0 Es el resultado esperado, una vez se empiece a realizar la actividad propuesta, por ejemplo, disminuir el 5 % del consumo de agua con respecto al consumo del ao 2018.
          div(numero="5" titulo="Actividad")
            p.mb-0 Es lo que debe realizar fsica o digitalmente para poder llevar a cabo los objetivos y las metas, por ejemplo, registrar el consumo mensual de agua en el formato FOR-BS-350- 068.
          div(numero="6" titulo="Accin")
            p.mb-0 Es la generalidad de las actividades realizadas, por ejemplo, registro de informacin de consumo.
          div(numero="7" titulo="Indicador")
            p.mb-0 Es la frmula mediante la cual se va a hacer el seguimiento del cumplimiento de las acciones del plan, por ejemplo, nmero de acciones ejecutadas / nmero de acciones programadas.
          div(numero="8" titulo="Soportes")
            p.mb-0 Evidencia fsica o digital de la realizacin de la accin, por ejemplo, formato de consumo de agua diligenciado mensualmente.
          div(numero="9" titulo="Recursos")
            p.mb-0 Cualquier medio, ya sea humano, tecnolgico o fsico, para poder cumplir con las actividades propuestas, por ejemplo, profesional ambiental, computador o facturas.
          div(numero="10" titulo="Responsable")
            p.mb-0 Persona encargada de realizar la actividad propuesta, por ejemplo, profesional ambiental.
          div(numero="11" titulo="Cronograma de actividades")
            p.mb-0 Meses en los cuales se va a llevar a cabo la actividad, por ejemplo, de enero a diciembre.
      .col-lg-4.d-lg-flex.d-none
        img(src="@/assets/curso/tema2/img29.jpg")

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="flip-up")
        .BGR13.p-3
          .row.justify-content-around.align-items-center
            .col-4.col-sm-3.col-lg-2.px-xl-5.px-4
              img(src="@/assets/curso/temas/img01.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1 Plan de trabajo de gestin ambiental
                  p.mb-0 Para conocer e identificar los componentes de un Plan Anual de Trabajo, se invita a consultar el PAT 2019 de la Superintendencia de Vigilancia y Seguridad privada (Supervigilancia), donde se programan las actividades a desarrollar, de acuerdo con el plan de gestin ambiental.
                .col-sm-auto
                  a.boton.color-primario(:href="obtenerLink('/downloads/PAT_2019_Supervigilancia.pdf')" target="_blank")
                    span(style="color: #000; font-weight: 400") Descargar
                    i.fas.fa-file-download(style="color: #000")

    .row.justify-content-start.mb-4
      .col-auto.BGRS01.px-5.py-3(data-aos="fade-right")
        .h4.text-white.mb-0 Sistema de Gestin Ambiental - SIGAM

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-1.col-2(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img30.png' alt="Imagen decorativa")
      .col(data-aos="fade-left")
        p.mb-0 #[strong La Corporacin Autnoma Regional - CAR], en su Escuela Virtual Ambiental (EVA), define el Sistema de Gestin Ambiental (SIGAM) como una propuesta organizacional para el adecuado funcionamiento de la administracin municipal, para adelantar la gestin ambiental en su territorio, mantener la oferta ambiental y orientar los procesos culturales y sociales hacia la sostenibilidad.

    .row.justify-content-center.mb-5
      .col-lg-6.col-md-10.mb-lg-0.mb-4(data-aos="fade-right") 
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema2/img31.png' alt="Imagen decorativa")
          .tarjeta.BGR02A.hg-03.p-4
            p.ms-5.mb-0 #[strong Objetivo del SIGAM:] es la realizacin de un proceso de anlisis, conceptualizacin y propuesta tcnica, orientado a desarrollar un modelo que gue la Gestin Ambiental Municipal (GAM) para alcanzar la sostenibilidad ambiental, social y econmica del municipio.
      .col-lg-6.col-md-10(data-aos="fade-left") 
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema2/img32.png' alt="Imagen decorativa")
          .tarjeta.BGR02A.hg-03.p-4
            p.ms-5.mb-0 #[strong Actores del SIGAM:] existen diferentes actores que se ven involucrados en el SIGAM, entre ellos los actores econmicos, que representan la pequea, mediana y gran industria, el comercio, las finanzas, el turismo, la venta de servicios, la actividad principal del municipio y asociaciones de gremios. Los actores sociales son representantes de inters colectivo, tales como habitantes urbanos y rurales, ONG, grupos ambientales, madres comunitarias y juntas de accin comunal.

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img33.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p La adopcin de un SIGAM se da mediante un acuerdo municipal donde se fijan los objetivos de la poltica y la gestin ambiental del municipio, se determina la dependencia responsable de la aplicacin, se definen funciones ambientales, se definen las entidades que lo conforman y se reglamenta el Consejo Ambiental Municipal.
        p.mb-0 El Consejo Ambiental Municipal es el coordinador de la poltica ambiental del municipio. Est conformado por el alcalde y las secretaras, as como por diferentes invitados, tales como representantes de las corporaciones autnomas regionales, Ministerio de ambiente, alcaldas vecinas, entre otros.

      
    .row.justify-content-center.align-items-end.mb-5
      .col-12.BG08.p-4(data-aos="fade-right")
        .row.justify-content-start
          .col-lg-9
            p Las funciones del Consejo ambiental municipal son varias, entre otras, deben:
            ul.lista-ul--color
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle(style="color: #4DB1F0;")
                | Asesorar a la Secretara de Medio Ambiente para la formulacin y fijacin de la poltica ambiental del municipio.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle(style="color: #4DB1F0;")
                | Proponer mecanismos institucionales, financieros y tcnicos que sean necesarios en el fortalecimiento y coordinacin de las entidades pertenecientes al SIGAM y para la coordinacin de este con las dems entidades del municipio.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle(style="color: #4DB1F0;")
                | Proponer al Concejo Municipal, por conducto del alcalde, las recomendaciones que considere pertinentes para adecuar el uso del territorio del municipio con los planes, programas y proyectos aprobados.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle(style="color: #4DB1F0;")
                | Servir de rgano de enlace entre la administracin municipal, el sector privado, la academia y las organizaciones sin nimo de lucro.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle(style="color: #4DB1F0;")
                | Recomendar a la autoridad ambiental competente, la adopcin de decisiones en materia de prevencin, control o mitigacin de los impactos ambientales generados por las diferentes actividades productivas del municipio.
      .col-lg-3.d-lg-flex.d-none.px-5.img-02(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img34.svg' alt="Imagen decorativa")
        
