              img(src="@/assets/curso/temas/img01.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1 Remisin Acuerdo 067 de 2017
                  p.mb-0 Para conocer e identificar los componentes de un sistema de gestin ambiental municipal, es necesario consultar el Acuerdo 067 de 2017, donde se ajusta el SIGAM de la ciudad de Medelln:
                .col-sm-auto
                  a.boton.color-primario(:href="obtenerLink('/downloads/Acuerdo_67_de_2017_sancionado.pdf')" target="_blank")
                    span(style="color: #000; font-weight: 400") Descargar
                    i.fas.fa-file-download(style="color: #000")

    .row.justify-content-start.mb-4
      .col-auto.BGRS01.px-5.py-3(data-aos="fade-right")
        .h4.text-white.mb-0 Plan Departamental de Agua - PDA
    
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img35.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p De acuerdo con el programa Agua y saneamiento para la prosperidad, el Plan Departamental de Agua (PDA) del Vichada (2017), es un conjunto de estrategias de planeacin y coordinacin interinstitucional, formuladas y ejecutadas con el objeto de lograr la armonizacin integral de los recursos y la implementacin de esquemas eficientes y sostenibles en la prestacin de los servicios pblicos domiciliarios de agua potable y saneamiento bsico, teniendo en cuenta las caractersticas locales, la capacidad institucional de las entidades territoriales, las personas prestadoras de los servicios pblicos y la implementacin efectiva de esquemas de regionalizacin.
        .cajon.color.color-primario.p-4
          p.mb-0 El Decreto 2246 de 2012 establece, dentro de los instrumentos de Planeacin de los PAP-PDA, el Plan ambiental, el cual define el componente ambiental de los PAP-PDA, y tiene por objeto considerar en la planeacin y ejecucin de los proyectos de prestacin de los servicios pblicos de acueducto, alcantarillado y aseo, los requerimientos ambientales asociados a dichos proyectos, para garantizar su sostenibilidad. 

    p El PDA, es un conjunto de estrategias de planeacin y coordinacin interinstitucional, formuladas y ejecutadas con el objeto de lograr la armonizacin integral de los recursos y la implementacin de esquemas eficientes y sostenibles.
    p.mb-5 A continuacin, se presentan los diferentes participantes que intervienen en el PDA:

    .BGR15.p-4.mb-5
      SlyderA(tipo="b" data-aos="zoom-in")
        .row.justify-content-center
          .col-lg-5
            .h4 Actores
            p Son las entidades estatales participantes en la coordinacin interinstitucional de los PDA:
            ul.lista-ul--color
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | El departamento.
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | Los municipios y/o distritos.
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | El Ministerio de Ambiente, Vivienda y Desarrollo Territorial (Mavdt).
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | El Departamento Nacional de Planeacin (DNP).
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | Las autoridades ambientales con jurisdiccin en los municipios ubicados en el territorio del respectivo departamento.
          .col-lg-5.col-10
            img(src='@/assets/curso/tema2/img36.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5
            .h4 Vinculados
            p.mb-0 Podrn tener la condicin de vinculados al PDA, previa aprobacin del comit directivo, las personas jurdicas de derecho pblico, privado o mixto, mediante el aporte de recursos financieros, tcnicos o humanos.
          .col-lg-5.col-10
            img(src='@/assets/curso/tema2/img37.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5
            .h4 Estructuras operativas
            p Igualmente hacen parte de los PDA, las siguientes estructuras operativas:
            ul.lista-ul--color
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | El comit directivo.
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | El gestor.
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | La gerencia asesora.
              li(style="margin-bottom: 5px")
                i.fas.fa-chevron-circle-right(style="color: #FFA031;")
                | El esquema fiduciario para el manejo de los recursos.
          .col-lg-5.col-10
            img(src='@/assets/curso/tema2/img38.png' alt="Imagen decorativa")

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="flip-up")
        .BGR13.p-3
          .row.justify-content-around.align-items-center
            .col-4.col-sm-3.col-lg-2.px-xl-5.px-4
              img(src="@/assets/curso/temas/img01.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1 Programa agua y saneamiento para la prosperidad - Plan Departamental de Agua del Vichada
                  p.mb-0 Para conocer e identificar los componentes de un Plan Departamental de Agua, se invita a consultar el PDA del departamento de Vichada 2017-2019.
                .col-sm-auto
                  a.boton.color-primario(:href="obtenerLink('/downloads/PDA_Vichada.pdf')" target="_blank")
                    span(style="color: #000; font-weight: 400") Descargar
                    i.fas.fa-file-download(style="color: #000")

    .row.justify-content-start.mb-4
      .col-auto.BGRS01.px-5.py-3(data-aos="fade-right")
        .h4.text-white.mb-0 Plan de Ordenamiento Territorial - POT
    
    .row.justify-content-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p De acuerdo con la Secretara Distrital de Planeacin (SDP) de la ciudad de Bogot, el Plan de Ordenamiento Territorial (POT) es el instrumento bsico, definido en la Ley 388 de 1997, para que los municipios y distritos del pas planifiquen el ordenamiento del territorio urbano y rural.
        p El POT contiene un conjunto de objetivos, polticas, estrategias, metas, normas, programas y proyectos que orientan el desarrollo fsico del territorio. 
        p.mb-0 En l se define la estrategia de cmo puede la ciudad hacer uso del suelo, en qu condiciones se puede localizar la vivienda, las actividades productivas, culturales y de esparcimiento. Revise a continuacin dichos elementos.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img39.png' alt="Imagen decorativa")


    TabsB.color-acento-contenido.mb-5(data-aos="zoom-in")
      .py-4.py-md-5(titulo="Funcin del POT" :icono="require('@/assets/curso/tema2/img40.png')")
        .row.justify-content-center
          .col-lg-6.order-lg-1.order-2
            p El POT orienta y prioriza las decisiones e intervenciones generales que debe hacer la administracin distrital, que permiten el desarrollo y mejoramiento de los parques, los andenes, las ciclorrutas, los centros de atencin a la poblacin, las vas, los servicios pblicos y los equipamientos; por eso es importante conocerlo, para que en los planes de desarrollo de cada una de las administraciones que lleguen, les den continuidad a los proyectos estratgicos.
            p.mb-0 Tambin prev las posibilidades de desarrollo de programas conjuntos entre el sector pblico y privado.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img44.png' alt="Imagen decorativa")
      .py-4.py-md-5(titulo="Objetivo del POT" :icono="require('@/assets/curso/tema2/img41.png')")
        .row.justify-content-center
          .col-lg-6.order-lg-1.order-2
            p.mb-0 El objetivo principal del POT se centra en mejorar la calidad de vida de los habitantes, mediante acceso a las oportunidades y beneficios que ofrece el desarrollo de la ciudad, as como tambin el desarrollo de equipamientos para los servicios sociales, la utilizacin racional del suelo, la sostenibilidad ambiental, la seguridad de la poblacin ante riesgos naturales y la preservacin del patrimonio.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img45.png' alt="Imagen decorativa")
      .py-4.py-md-5(titulo="La importancia del POT" :icono="require('@/assets/curso/tema2/img42.png')")
        .row.justify-content-center
          .col-lg-6.order-lg-1.order-2
            p Es el orientador de las decisiones que toman los alcaldes; la planificacin del territorio sirve como soporte para gestionar los recursos adicionales de financiacin ante las entidades del Estado que permiten la ejecucin de proyectos e infraestructuras.
            p.mb-0 Permite un uso racional del suelo, condiciones para el reparto equitativo de cargas y beneficios, relacin equilibrada con el medio ambiente, preservacin del patrimonio cultural, fortalecimiento de los agentes que influyen en el mejoramiento de la calidad de vida y la generacin de recursos propios.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img46.png' alt="Imagen decorativa")
      .py-4.py-md-5(titulo="Ordenamiento del territorio" :icono="require('@/assets/curso/tema2/img43.png')")
        .row.justify-content-center
          .col-lg-6.order-lg-1.order-2
            p Es un proceso por medio del cual se orienta el desarrollo del territorio y se encuentra fundamentado en tres principios, de acuerdo con la Ley 388 de 1997, as:
            ol.lista-ol--cuadro.mb-0
              li 
                .lista-ol--cuadro__vineta
                  span 1
                | La funcin social y ecolgica del territorio.
            ol.lista-ol--cuadro.mb-0
              li 
                .lista-ol--cuadro__vineta
                  span 2
                | La prevalencia del inters general sobre el particular.
            ol.lista-ol--cuadro.mb-0
              li 
                .lista-ol--cuadro__vineta
                  span 3
                | La distribucin equitativa de las cargas y los beneficios, con base en estrategias de desarrollo socioeconmico y en armona con el medio ambiente, que se ajustan a una imagen objetivo que es fijada previamente con el consenso de la comunidad.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img47.png' alt="Imagen decorativa")
    
    p Tambin define los permetros de las zonas que se encuentran clasificadas como reas protegidas.

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="flip-up")
        .BGR13.p-3
          .row.justify-content-around.align-items-center
            .col-4.col-sm-3.col-lg-2.px-xl-5.px-4
              img(src="@/assets/curso/temas/img01.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1 Plan de Ordenamiento Territorial
                  p.mb-0 Se sugiere conocer los diferentes tomos del Plan de Ordenamiento Territorial (POT) de la ciudad de Bogot para el ao 2020-2021. 
                .col-sm-auto
                  a.boton.color-primario(href="https://www.sdp.gov.co/micrositios/pot/documentos" target="_blank")
                    span(style="color: #000; font-weight: 400") Ver enlace
                    i.fas.fa-link(style="color: #000")

    .row.justify-content-start.mb-4
      .col-auto.BGRS01.px-5.py-3(data-aos="fade-right")
        .h4.text-white.mb-0 Plan de Gestin Integral de Residuos Hospitalarios y Similares - PGIRH
    
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img48.svg' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p De acuerdo con la resolucin 1164 (2002) el Plan de Gestin Integral de Residuos Hospitalarios y Similares (PGIRH) es el documento diseado por los generadores, los prestadores del servicio de desactivacin y especial de aseo, el cual contiene, de una manera organizada y coherente, las actividades necesarias que garantizan la Gestin Integral de los Residuos Hospitalarios.
        .cajon.color-primario.p-4
          p.mb-0 Los generadores, prestadores del servicio de desactivacin y prestadores del servicio especial de aseo de residuos hospitalarios y similares, disearn e implementarn el PGIRH de acuerdo con las actividades que desarrollen, teniendo como punto de partida su compromiso institucional de carcter sanitario y ambiental, el cual debe ser real, claro, con propuestas de mejoramiento continuo de los procesos y orientado a la minimizacin de riesgos para la salud y el medio ambiente.

    .row.justify-content-center.mb-4
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
